import React, { CSSProperties } from 'react';
import { Counter } from './counter';
import { CurrencyIcon } from './icons';
import clsx from 'clsx';

// import './burger-ingredient.css';
import css from './burger-ingredient.css';

type TBurgerIngredientProps = {
    className?: string;
    image: string;
    price: number;
    name: string;
    count: number;

    style?: CSSProperties;
};

export const BurgerIngredient: React.FC<TBurgerIngredientProps> = ({
    image,
    price,
    name,
    count,
    style,
    className,
}) => {
    return (
        <div
            className={clsx('burger-ingredient', className, css.qwe)}
            style={style}
        >
            {count && <Counter count={count} />}
            <img src={image} alt=' .' />
            <div className="burger-ingredient__cost mt-2 mb-2">
                <p className='text text_type_digits-default mr-2'>{price}</p>
                <CurrencyIcon type='primary' />
            </div>
            <p className="burger-ingredient__text text text_type_main-default">{name}</p>
        </div>
    );
};
